.pumpingStation{
  width: 2327px;
  height: 853px;
  .yunxing{
    width: 384px;
    text-align: center;
    height: 103px;
    // border-image: url(../../assets/imgs/标题框.png) 0 0 0 0 fill round;
    p{
      font-size: 44px;
      line-height: 103px;
      letter-spacing: 2px;
      color: #ffffff;
      text-shadow: 0 0 30px rgb(244, 250, 245);
    }
  }
  .drainagePump{
    width: 2321px;
    height: 721px;
    // border-image: url(../../assets/imgs/框.png) 1 0 0 0 fill round;
    // border-image-repeat: stretch;
    margin-top: 29px;
    overflow: hidden;
    .bzbeijin{
      display: inline-block;
      margin-top: 116px;
      margin-left: 82px;
      width: 1040px;
      height: 503px;
      .psone{
        width: 488px;
        height: 122px;
        // background-image: url(../../assets/imgs/泵站.png);
        font-size: 40px;
        color: #fff;
        text-align: center;
        letter-spacing: 1px;
        line-height: 102px;
      }
      .condition{
        margin-top: 29px;
        margin-left: 103px;
        width: 781px;
        height: 94px;
        p{
          float: left;
          display:inline-block;
          height: 94px;
          line-height: 94px;
          font-size: 40px;
          letter-spacing: 1px;
          color: #00ffff;
        }
        .stop{
          margin-left: 45px;
              float: left;
              width: 46px;
              height: 94px;
          span{
            display:inline-block;
            width: 46px;
            height: 59px;
            // background-image: url(../../assets/imgs/状态-运行.png);
       
          }
          i{
            font-size: 30px;
            font-style: normal;
            letter-spacing: 2px;
            color: #0090ff;
          }
        }   
      }

      // 进度条
      .jindu{
        margin-top: 44px;
        margin-left: 103px;
        height: 40px;
        .shunshi{
          width: 160px;
          height: 40px;
          float: left;
          font-size: 38px;
          color: #00ffff;
          letter-spacing: 1px;
        }
        .Time{
          font-size: 45px;
          color: #00ffff;
          span{
            font-size: 35px;
          }
        }
      }
      .container {
        margin-top: 20px;
        margin-left: 40px;
        margin-right: 5px;
        float: left;
         width:456px;
        } 
      .title {
        margin-left: 20px;
         background:#545965; 
         color:#fff; 
         padding:15px; 
         float:left;
        position:relative;
         -webkit-border-top-left-radius:5px; 
         -webkit-border-bottom-left-radius:5px; 
         -moz-border-radius-topleft:5px; 
         -moz-border-radius-bottomleft:5px; 
         border-top-left-radius:5px; 
         border-bottom-left-radius:5px 
        }
      .bar { 
        height:18px; 
        display:block; 
        background:#0647bb;
        width:100%;
        border-radius:8px 
      }
      .bar1 {
         height:18px;
          display:block;
          background:#00ffff;
          width:0;
          border-radius:8px;
          -webkit-transition:width .8s ease;
          -moz-transition:width .8s ease;
          transition:width .8s ease;
          -webkit-animation:progressbar 7s infinite;
          animation:progressbar 7s infinite }
      @-webkit-keyframes progressbar { from { width:0 } to { width:60% } } /* Standard syntax */ @keyframes progressbar { from { width:0 } to { width:60% } }
    }
  }
}