.pumpingStation {
  width: 2327px;
  height: 853px;
}
.pumpingStation .yunxing {
  width: 384px;
  text-align: center;
  height: 103px;
}
.pumpingStation .yunxing p {
  font-size: 44px;
  line-height: 103px;
  letter-spacing: 2px;
  color: #ffffff;
  text-shadow: 0 0 30px #f4faf5;
}
.pumpingStation .drainagePump {
  width: 2321px;
  height: 721px;
  margin-top: 29px;
  overflow: hidden;
}
.pumpingStation .drainagePump .bzbeijin {
  display: inline-block;
  margin-top: 116px;
  margin-left: 82px;
  width: 1040px;
  height: 503px;
  /* Standard syntax */
}
.pumpingStation .drainagePump .bzbeijin .psone {
  width: 488px;
  height: 122px;
  font-size: 40px;
  color: #fff;
  text-align: center;
  letter-spacing: 1px;
  line-height: 102px;
}
.pumpingStation .drainagePump .bzbeijin .condition {
  margin-top: 29px;
  margin-left: 103px;
  width: 781px;
  height: 94px;
}
.pumpingStation .drainagePump .bzbeijin .condition p {
  float: left;
  display: inline-block;
  height: 94px;
  line-height: 94px;
  font-size: 40px;
  letter-spacing: 1px;
  color: #00ffff;
}
.pumpingStation .drainagePump .bzbeijin .condition .stop {
  margin-left: 45px;
  float: left;
  width: 46px;
  height: 94px;
}
.pumpingStation .drainagePump .bzbeijin .condition .stop span {
  display: inline-block;
  width: 46px;
  height: 59px;
}
.pumpingStation .drainagePump .bzbeijin .condition .stop i {
  font-size: 30px;
  font-style: normal;
  letter-spacing: 2px;
  color: #0090ff;
}
.pumpingStation .drainagePump .bzbeijin .jindu {
  margin-top: 44px;
  margin-left: 103px;
  height: 40px;
}
.pumpingStation .drainagePump .bzbeijin .jindu .shunshi {
  width: 160px;
  height: 40px;
  float: left;
  font-size: 38px;
  color: #00ffff;
  letter-spacing: 1px;
}
.pumpingStation .drainagePump .bzbeijin .jindu .Time {
  font-size: 45px;
  color: #00ffff;
}
.pumpingStation .drainagePump .bzbeijin .jindu .Time span {
  font-size: 35px;
}
.pumpingStation .drainagePump .bzbeijin .container {
  margin-top: 20px;
  margin-left: 40px;
  margin-right: 5px;
  float: left;
  width: 456px;
}
.pumpingStation .drainagePump .bzbeijin .title {
  margin-left: 20px;
  background: #545965;
  color: #fff;
  padding: 15px;
  float: left;
  position: relative;
  -webkit-border-top-left-radius: 5px;
  -webkit-border-bottom-left-radius: 5px;
  -moz-border-radius-topleft: 5px;
  -moz-border-radius-bottomleft: 5px;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
}
.pumpingStation .drainagePump .bzbeijin .bar {
  height: 18px;
  display: block;
  background: #0647bb;
  width: 100%;
  border-radius: 8px;
}
.pumpingStation .drainagePump .bzbeijin .bar1 {
  height: 18px;
  display: block;
  background: #00ffff;
  width: 0;
  border-radius: 8px;
  -webkit-transition: width 0.8s ease;
  -moz-transition: width 0.8s ease;
  transition: width 0.8s ease;
  -webkit-animation: progressbar 7s infinite;
  animation: progressbar 7s infinite;
}
@-webkit-keyframes progressbar {
  from {
    width: 0;
  }
  to {
    width: 60%;
  }
}
@keyframes progressbar {
  from {
    width: 0;
  }
  to {
    width: 60%;
  }
}
